<template>
  <div id="app" class="bg">

    <NavBar :signOut="signOut" :authenticated="authenticated" @pageChange="page = $event"/>
    <b-card>
      <google-auth v-if="id_token == ''" :attachSignin="attachSignin" @authenticated="auth2 = $event"/>
      <Home v-if="page == 'home'"/>
      <UserProfile v-if="page == 'profile'"/>
    </b-card>

  </div>
</template>

<script>

import NavBar from './components/NavBar.vue'

export default {
  name: 'app',
  components: {
    Home: () => import("./components/Home.vue"),
    NavBar,
    UserProfile: () => import("./components/UserProfile.vue")
  },
  data: function() {
    return {
      auth2: null,
      id_token: '',
      authenticated: false,
      page: 'login'
    }
  },
  methods: {
    attachSignin(element) {
      var vm = this;
      this.auth2.attachClickHandler(element, {},
          function(googleUser) {
            vm.id_token = googleUser.getAuthResponse().id_token;
            vm.authenticated = true;
            vm.page = 'home';
          }, function(error) {
            alert(JSON.stringify(error, undefined, 2));
          });
    },
    signOut() {
      var vm = this;
      this.auth2.signOut().then(function () {
        vm.id_token = '';
        vm.authenticated = false;
        vm.page = 'login';
      });
    },
    setPage(value) {
      this.page = value;
    }
  }
}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }

  html {
    background-color: #eeeeee;
    height: 100%;
  }
</style>
